import React, { Component } from "react";
import PrintTodo from "./PrintTodo";
import { Button, FormControl, InputGroup } from "react-bootstrap";

class Todos extends Component {
  constructor(props) {
    super(props);
    this.state = {
      todos: ["abdul", "rehman", "kaim"],
      tempValue: "",
      editMode: false,
      idTodo: null
    };
  }

  renderTodos = () => {
    console.log("value", this.state.tempValue);
    const todos = [...this.state.todos, this.state.tempValue]; // this will add new value
    this.setState({
      todos
    });
  };
  deleteTodo = key => {
    const todos = this.state.todos.filter((
      value,
      index // this will delete specific value
    ) => {
      if (key != index) {
        // console.log(  'key' , key , 'index' , index);
        // console.log(  'key' , typeof(key), 'index' , typeof(index));
        return value;
      }
    });
    // const  todos = [...this.state.todos , value];  // this will add new value
    this.setState({
      todos
    });
  };

  editTodo = index => {
    console.log("value", this.state.todos[index]);
    this.setState({
      editMode: true,
      tempValue: this.state.todos[index],
      idTodo: index
    });
  };

  cancelUpdate = () => {
    console.log("cancelUpdate");
    this.setState({
      editMode: false,
      tempValue: "",
      idTodo: ""
    });
  };
  updateTodo = () => {
    // console.log("updateTodo");
    let { editMode, tempValue, idTodo, todos } = this.state;
   
    todos[idTodo] =  tempValue ;
    
    // console.log("updateTodo"  , todos );
    this.setState({
      editMode: false,
      tempValue: '',
      idTodo: '',
      todos
    });
  };

  render() {
    const { tempValue, editMode } = this.state;
    return (
      <div className="container App">
        <div>
          <InputGroup className="mb-3">
            <FormControl
              placeholder="Add todos..."
              aria-label="Recipient's username"
              aria-describedby="basic-addon2"
              value={tempValue}
              onChange={event =>
                this.setState({ tempValue: event.target.value })
              }
              onKeyPress={event => {
                if (event.key === "Enter") {
                  console.log("temp Enter");
                  this.renderTodos();
                }
              }}
            />
            <InputGroup.Append>
              <Button
                variant="outline-secondary"
                onClick={editMode ? this.updateTodo : this.renderTodos}
              >
                {editMode ? "Update" : "Add"}
              </Button>
              {editMode && (
                <Button variant="outline-secondary" onClick={this.cancelUpdate}>
                  Cancel
                </Button>
              )}
            </InputGroup.Append>
          </InputGroup>
        </div>

        <PrintTodo
          oTodos={this.state.todos}
          deleteTodo={this.deleteTodo}
          editTodo={this.editTodo}
        />
      </div>
    );
  }
}

export default Todos;
